
export const requireAuthentication = (req, res, next) => {
    if (req.isAuthenticated()) {
        return next();
    }

    return res.redirect('/auth/validate/fail/0')
}

export const requireNotAuthentication = (req, res, next) => {
    if (req.isAuthenticated()) {
        return res.redirect('/auth/badreq')
    }

    return next();
}

export const requireCashier = (req, res, next) => {
    if (req.user.role_id == 1) {
        return next();
    }

    return res.redirect('/auth/validate/fail/1');
}

export const requireAdmin = (req, res, next) => {
    if (req.user.role_id == 2) {
        return next();
    }

    return res.redirect('/auth/validate/fail/2');
}

export const requireSuperAdmin = (req, res, next) => {
    if (req.user.role_id == 3) {
        return next();
    }

    return res.redirect('/auth/validate/fail/3');
}